header {
  height: 100vh;
  padding-top: 3.5rem;
  overflow: hidden;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

/* :::::::::: CTA ::::::::::::: */

.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/* :::::::::: SOCIAL ICONS ::::::::::::: */

.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem; 
  z-index: 2;
}

.header__socials::after {
  content: '';
  width: 1px;
  height: 3.5rem;
  background: var(--color-primary);
}

/* :::::::::: IMAGE (ME) ::::::::::::: */

.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 30rem;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 4rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 7rem 0 1.5rem 0;
  z-index: 1;
}

/* :::::::::: SCROLL__DOWN ::::::::::::: */

.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

/* :::::::::::: MEDIA QUERIES (MD) ::::::::::::::: */

@media screen and (max-width: 1024px) {
  header {
    height: 100vh;
  }
}

/* :::::::::::: MEDIA QUERIES (SM) ::::::::::::::: */

@media screen and (max-width: 600px) {
  header {
    height: 100vh;
  }
}
