.container.testimonials__container {
  width: 100%;
}

.clients__avatar {
  width: 5rem;
  aspect-ratio: 1/1;
  overflow: hidden;
  border-radius: 50%;
  margin: 0 auto 1rem;
  border: 0.16rem solid var(--color-primary-variant);
}

.testimonial {
  background: var(--color-bg-variant);
  text-align: center;
  padding: 2rem;
  border-radius: 2rem;
  border: 1px solid transparent;
  user-select: none;
}

.testimonial:hover {
  background: transparent;
  backdrop-filter: blur(5px);
  border-color: var(--color-primary-variant) !important;
  cursor: default;
}

.client__review {
  color: var(--color-light);
  font-weight: 300;
  display: block;
  width: 80%;
  margin: 0.8rem auto 0.2rem;
  padding-bottom: 1rem;
}

/* :::::::::::: MEDIA QUERIES (SM) ::::::::::::::: */

@media screen and (max-width: 600px) {
  .client__name {
    font-size: 0.8rem;
  }
  .client__review {
    font-size: 0.7rem;
  }
  .testimonial {
    padding: 0.5rem;
  }
  .clients__avatar {
    width: 4rem;
  }
}