nav {
  background: var(--color-nav-bg);
  width: max-content;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.7rem 1.7rem;
  z-index: 2;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 2rem;
  display: flex;
  gap: 0.8rem;
  border-radius: 3rem;
  backdrop-filter: blur(15px);
}

nav a {
  background: transparent;
  padding: 0.9rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-light);
  font-size: 1.1rem;
  cursor: pointer;
}

nav a:hover {
  background-color: var(--color-nav-bg);
}

nav a.active {
  background-color: var(--color-nav-bg);
  color: var(--color-white);
}

/* :::::::::::: DROPDOWN ANCHORS ::::::::::::::: */

.anchorbtn {
  background: transparent;
  padding: 0.9rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-light);
  font-size: 1.1rem;
  border: none;
  cursor: pointer;
}

/* The container <div> - needed to position the dropdown content */
.anchor {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/* Dropdown Content (Hidden by Default) */
.anchor-content {
  display: none;
  position: absolute;
  padding-bottom: 3rem;
  background: rgba(255, 255, 255, 0.6);
  min-width: auto;
  box-shadow: 0px 8px 16px 0px var(--color-black);
  transform: translateY(-41.2%);
  border-radius: 5rem;
  z-index: -1;
}

/* Links inside the dropdown */
.anchor-content a {
  color: var(--color-bg);
  padding: 0.8rem;
  text-decoration: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/* Change color of dropdown links on hover */
.anchor-content a:hover {
  background:  var(--color-nav-bg);
}

/* Show the dropdown menu on hover */
.anchor:hover .anchor-content {
  display: block;
}

/* Change the background color of the dropdown button when the dropdown content is shown */
.anchor:hover .anchorbtn {
  background-color:  var(--color-nav-bg);
  color: var(--color-primary);
}

/* :::::::::::: DROPDOWN LANGUAGE ::::::::::::::: */

.langbtn {
  background: transparent;
  padding: 0.9rem;
  border-radius: 5%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-light);
  font-size: 1.1rem;
  border: none;
  cursor: pointer;
}

/* The container <div> - needed to position the dropdown content */
.lang {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/* Dropdown Content (Hidden by Default) */
.lang-content {
  display: none;
  position: absolute;
  border-radius: 5%;
  padding-bottom: 3rem;
  background:rgba(255, 255, 255, 0.6);
  min-width: auto;
  box-shadow: 0px 8px 16px 0px var(--color-black);
  transform: translateY(-34.6%);
  z-index: -1;
}

/* Links inside the dropdown */
.lang-content a {
  color: var(--color-bg);
  width: 100%;
  font-size: 0.71rem;
  padding: 0.5rem;
  text-decoration: none;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: stretch;
}

/* Change color of dropdown links on hover */
.lang-content a:hover {
  background:  var(--color-nav-bg);
  border-radius: 0 !important;
}

/* Show the dropdown menu on hover */
.lang:hover .lang-content {
  display: block;
}

/* Change the background color of the dropdown button when the dropdown content is shown */
.lang:hover .langbtn {
  background-color:  var(--color-nav-bg);
  color: var(--color-primary);
  /* border-radius: 5% */
}

/* :::::::::::: MEDIA QUERIES (SM) ::::::::::::::: */

@media screen and (max-width: 600px) {
  nav {
    width: min-content;
    gap: 0.15rem;
  }
}