@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&display=swap');

/* :::::::::::: ROOT ::::::::::::::: */


* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;

}

:root {
  --color-bg: #1f1f38;
  --color-bg-variant: #212045;
  --color-primary: #4db5ff;
  --color-primary-variant: rgba(77, 181, 255, 0.4);
  --color-white: #fff;
  --color-black: #000;
  --color-light: rgba(255, 255, 255, 0.6);

  --color-nav-bg:  rgba(0, 0, 0, 0.3);

  --general-transtion: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;

  --default-font-weight: 500;
  --h1-font-size: 2.5rem;

  --section-margin-top-lg: 8rem;
  --section-margin-top-md: 6rem;

  --section-title-margin-bottom-lg: 3rem;
  --section-title-margin-bottom-sm: 2rem;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: 'Poppins', 'Montserrat', sans-serif;
  background: var(--color-bg);
  color: var(--color-white);
  line-height: 1.7;
}

/* :::::::::::: GENERAL ::::::::::::::: */

.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1, h2, h3, h4, h5 {
  font-weight: var(--default-font-weight);
}

h1 {
  font-size: var(--h1-font-size);
}

section {
  margin-top: var(--section-margin-top-lg);
}

section > h2, section > h5 {
  text-align: center;
  color: var(--color-light);
}

section > h2 {
  color: var(--color-primary);
  margin-bottom: var(--section-title-margin-bottom-lg);
}

.text-light {
  color: var(--color-light);
}

a {
  color: var(--color-primary);
  transition: var(--general-transtion);
}

a:hover {
  color: var(--color-white);
}

.btn {
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--general-transtion);
  backdrop-filter: blur(3px);
}

.btn:hover {
  background: var(--color-white);
  color: var(--color-bg);
  border: 1px solid transparent;
  box-shadow: none;
}

.btn-primary {
  background: var(--color-primary);
  color: var(--color-bg);
  box-shadow: 0px 0px 15px var(--color-primary), 0px 0px 35px var(--color-primary) inset;
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}


/* :::::::::::: MEDIA QUERIES (MD) ::::::::::::::: */

@media screen and (max-width: 1024px) {
  .container {
    width: var(--container-width-md);
  }

  section {
    margin-top: var(--section-margin-top-md);
  }
}

/* :::::::::::: MEDIA QUERIES (SM) ::::::::::::::: */

@media screen and (max-width: 600px) {
  .container {
    width: var(--container-width-sm);
  }

  section > h2 {
    margin-top: var(--section-title-margin-bottom-sm);
  }
}
